import Vue from 'vue'
import Router from 'vue-router'
import {
  store
} from './../store'

import {
  routes
} from './routes'

Vue.use(Router)

let router = new Router({
  routes,
  mode: 'history'
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (store.getters.isLoggedIn) {
      next()
      return
    }
    next('/login')
  } else {
    next()
  }
})

export default router



